.rma {
  margin: 78px 0 140px;
  h3 {
    margin-bottom: 52px;
    font-size: 22px;
    color: #1f1f1f;
  }
  .shipping {
    padding: 40px 0 65px;
    border-top: 4px solid #000;
    h4 {
      font-weight: bold;
      margin-bottom: 35px;
    }
    dl {
      color: #898989;
      font-size: 14px;
      line-height: 1.8571;
      dt,
      dd {
        float: left;
        margin-bottom: 16px;
      }
      dt {
        clear: left;
        width: 130px;
      }
      dd {
        float: left;
        em {
          color: #1f1f1f;
        }
      }
      &::after {
        content: '';
        display: block;
        clear: both;
      }
    }
    .note {
      font-size: 13px;
      margin-top: 40px;
      padding-top: 15px;
      border-top: 1px solid #f1f1f1;
      h5 {
        margin: 25px 0 18px;
      }
      li {
        color: #aaa;
        line-height: 1.6923;
        position: relative;
        padding: 0 0 10px 10px;
        &::before {
          content: '';
          position: absolute;
          top: 8px;
          left: 0;
          width: 2px;
          height: 2px;
          background-color: #c1c1c1;
        }
      }
    }
    .gifts {
      margin-left: -25px;
      li {
        float: left;
        width: 473px;
        margin-left: 25px;
        .title {
          font-size: 14px;
          margin-bottom: 20px;
        }
        .gift {
          color: #1f1f1f;
          padding: 0 16px;
          background-color: #f7f7f7;
          .thumb {
            vertical-align: middle;
            display: inline-block;
            width: 100px;
            margin-right: 10px;
          }
          .cnt {
            vertical-align: middle;
            display: inline-block;
            .brand {
              font-size: 11px;
              font-weight: bold;
              margin-bottom: 10px;
            }
            .title {
              color: #898989;
              font-size: 16px;
              margin-bottom: 0;
            }
          }
        }
      }
      &::after {
        content: '';
        display: block;
        clear: both;
      }
    }
  }
  .order-guide {
    line-height: 1.4;
    text-align: right;
    height: 0;
    a {
      font-size: 13px;
      position: relative;
      display: inline-block;
      top: -33px;
      height: 33px;
      padding-right: 17px;
      background: url('/_ui/img/pc/icn/icn-order-guide.png') no-repeat 100% 2px;
    }
  }
  .order-search {
    margin-bottom: 60px;
    padding: 45px 40px 35px;
    background-color: #f8f8f8;
    .row {
      width: 100%;
      height: 50px;
      .col {
        width: 50%;
        white-space: nowrap;
        float: left;
        .label {
          font-size: 14px;
          text-align: left;
          line-height: 40px;
          box-sizing: border-box;
          float: left;
          margin-bottom: 10px;
          padding-right: 19px;
          &.right {
            text-align: right;
          }
        }
        input {
          box-sizing: border-box;
          float: left;
          height: 40px;
          padding: 0 10px;
          border: 1px solid #dedede;
        }
        .select {
          float: left;
          select {
            box-sizing: border-box;
            height: 40px;
            padding: 0 10px;
            background-color: #fff;
            border: 1px solid #dedede;
          }
        }
        .split {
          text-align: center;
          line-height: 40px;
          float: left;
          width: 17px;
        }
      }
      .col.type {
        //width: 245px;
        .label {
          //width: 75px;
          width: 20%;
        }
        .select {
          //width: 170px;
          width: 80%;
        }
      }
      .col.progress {
        .label {
          width: 20%;
        }
        .select {
          width: 80%;
        }
      }
      .col.product {
        width: 100%;
        //width: 547px;
        .label {
          width: 10%;
          //width: 84px;
        }
        .select {
          width: 19%;
          //width: 171px;
        }
        input {
          width: 70%;
          float: right;
          //width: 282px;
          //margin-left: 10px;
        }
      }
      .col.month {
        .label {
          width: 20%;
        }
        .tab {
          display: inline-block;
          width: 80%;
          //width: 341px;
          padding-bottom: 0;
          background-color: #fff;
          li {
            width: 25%;
            a {
              line-height: 38px;
              height: 38px;
              padding: 0;
            }
          }
          .on {
            background-color: #898989;
          }
        }
      }
      .col.date {
        //margin-left: 40px;
        .date {
          margin-left: 25px;
        }
        input {
          width: 152px;
          padding: 0 10px;
          cursor: pointer;
        }
      }
      .btn {
        float: right;
        width: 20%;
        margin-left: 10px;
      }
      &::after {
        content: '';
        display: block;
        clear: both;
      }
    }
  }
  .billing {
    font-size: 14px;
    padding: 40px 0 60px;
    border-top: 4px solid #000;
    h4 {
      font-weight: bold;
      margin-bottom: 35px;
    }
    .table {
      display: table;
      table-layout: fixed;
      width: 100%;
      margin-bottom: 30px;
      .col {
        position: relative;
        display: table-cell;
        padding: 0 30px;
        h5 {
          margin: 8px 0 30px;
          .tip {
            width: 15px;
            height: 15px;
            margin-left: 7px;
            background: url('/_ui/img/pc/icn/icn-tip.png') no-repeat;
            text-indent: -9999px;
            vertical-align: baseline;
          }
        }
        li {
          margin-top: 20px;
          strong {
            color: #898989;
            font-weight: normal;
            float: left;
          }
          .price {
            color: #1f1f1f;
            float: right;
            text-align: right;
            line-height: 18px;
            &.bank {
              display: inline-block;
              margin-top: 5px;
              line-height: 20px;
              color: #898989;
            }
            .virtual-info {
              margin-top: 20px;
              span {
                display: inline-block;
                color: #898989;
                padding-top: 4px;
              }
            }
          }
          &::after {
            content: '';
            display: block;
            clear: both;
          }
        }
        .receipt {
          position: absolute;
          top: 0;
          right: 30px;
          .btn-small {
            height: 24px;
            padding: 0 13px;
            font-size: 12px;
            line-height: 24px;
          }
        }
        .refund-type {
          text-align: right;
          margin-top: 20px;
          .radio {
            margin-left: 15px;
          }
        }
        &:first-child {
          padding-left: 0;
          &::before {
            display: none;
          }
        }
        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          display: block;
          width: 1px;
          height: 100%;
          background-color: #f1f1f1;
        }
      }
    }
    .total {
      color: #1f1f1f;
      font-size: 20px;
      text-align: right;
      padding: 30px;
      border-top: 1px solid #f1f1f1;
      background: #fafafa;
      em {
        color: #f23d4e;
        font-weight: bold;
        margin-left: 15px;
      }
    }
  }
  .buttons.end-row {
    margin-top: 0;
    padding-top: 0;
    border-top: 0;
    .btn-billing {
      background-color: #f23d4e;
    }
  }
  .desc {
    margin-bottom: 5px;
    font-size: 14px;
    color: #898989;
  }
  .order-lists {
    .price {
      width: 100px;
      strong,
      span {
        display: block;
      }
      strong {
        color: #898989;
        font-weight: normal;
        margin-bottom: 5px;
      }
    }
    .delivery {
      width: 150px;
    }
    .head {
      overflow: hidden;
      padding-top: 32px;
      span {
        float: left;
        display: block;
        font-size: 13px;
        color: #898989;
      }
      .tit-1 {
          width: 630px;
      }
      .tit-2,
      .tit-3,
      .tit-5 {
          text-align: center;
          width: 100px;
      }
      .tit-3 {
          width: 167px;
      }
      .tit-4 {
          width: 70px;
      }
    }
  }
}

.layer-rma {
  .layer {
    font-size: 16px;
    .bul-list {
      li {
        position: relative;
        margin-top: 10px;
        padding-left: 8px;
        font-size: 14px;
        color: #898989;
        white-space: normal;
        word-wrap: break-word;
        -ms-word-wrap: break-word;
        &:first-child {
          margin-top: 0;
        }
        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 10px;
          width: 2px;
          height: 2px;
          background-color: #c1c1c1;
        }
      }
    }
  }
}
